import { RESULT_HANDLER } from '../actions/actions'

const handlerMessage = {
  NO_GEOCODE_RESULTS:
    'Przykro mi, nie mona odnale lokalizacji pod podanym adresem.',
  NO_ISOCHRONES_RESULTS:
    'Przykro mi, nie mona oszacowa izochrony dla wybranej lokalizacji.'
}

const handlerTopic = {
  NO_GEOCODE_RESULTS: 'Nie mog odnale adresu',
  NO_ISOCHRONES_RESULTS: 'Nie mog zbudowa izochrony dla tej lokalizacji',
  INVALID_CREDENTIALS:
    'Twoje dane s nieprawidowe.'
}

const initialState = {
  handlerCode: null,
  handlerMessage: null,
  handlerTopic: null,
  receivedAt: null
}

const resultHandler = (state = initialState, action) => {
  console.log(action)
  switch (action.type) {
    case RESULT_HANDLER:
      return {
        ...state,
        handlerCode: action.handlerCode,
        handlerTopic: handlerTopic[action.handlerCode],
        handlerMessage: action.handlerMessage
          ? action.handlerMessage
          : handlerMessage[action.handlerCode],
        receivedAt: action.receivedAt
      }
    default:
      return state
  }
}

export default resultHandler
